/* imports */
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);

#root {
  padding: 0;
  height: auto;
  text-align: center;
}

header {
  position: fixed;
  top: 0;
  z-index: 1000;
  padding: 1vh 5vw;
  width: calc(100% - 10vw);
  display: flex;
  justify-content: space-between;
  transition: all 0.2s ease-in-out;

  #logo > svg {
    max-width: 3vw;
    min-width: 50px;
  }

  #list {
    display: flex;
    align-items: center;
  }

  #list > * {
    font-family: Roboto;
    font-size: 1.1em;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    /* 100% */
    letter-spacing: 2.4px;
    text-transform: uppercase;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: auto;
  }

  li {
    float: left;
    padding: 0 4vw;
  }
}

#present {
  height: 100vh;
  background-color: aqua;

  .present_content {
    position: absolute;
    margin-top: calc(header[height]);
    background-color: red;
  }
}
